/* Существующие стили для player-menu */
.player-menu {
    position: fixed;
    bottom: 15px;
    left: 50%;
    transform: translateX(-50%);
    height: 50px;
    width: calc(100% - 30px);
    max-width: 670px;
    background-image: url('img/ui/menubg.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
    z-index: 1000;
    border-radius: 8px;
    transition: all 0.3s ease;

    /* Добавляем flex для размещения элементов внутри меню */
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
}

.player-menu:hover {
    box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.25);
    transform: translateX(-50%) translateY(-2px);
}

/* Секции меню */
.menu-left-section {
    flex: 1;
}

.menu-right-section {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: relative;
}

/* Контейнер кнопки-скролла */
.scroll-button-container {
    position: relative;
}

/* Стиль кнопки-скролла */
.scroll-button {
    background: rgba(40, 40, 40, 0.8);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 6px;
    color: white;
    padding: 6px 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.2s ease;
    outline: none;
    position: relative;
    width: 60px;
    height: 36px;
}

.scroll-button:hover {
    background: rgba(60, 60, 60, 0.8);
    border-color: rgba(255, 255, 255, 0.5);
}

.bpm-value {
    font-size: 14px;
    font-weight: bold;
}

.bpm-label {
    font-size: 10px;
    opacity: 0.7;
}

/* Стрелки для скроллинга */
.scroll-arrows {
    position: absolute;
    right: -20px;
    top: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.scroll-arrow {
    background: transparent;
    border: none;
    color: rgba(255, 255, 255, 0.6);
    font-size: 10px;
    cursor: pointer;
    padding: 2px;
    transition: color 0.2s ease;
}

.scroll-arrow:hover {
    color: white;
}

/* Раскрывающееся меню выбора BPM */
.bpm-selector-menu {
    position: absolute;
    top: -110px; /* Размещаем выше кнопки */
    right: 0;
    width: 120px;
    background: rgba(20, 20, 20, 0.9);
    border-radius: 8px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
    overflow: hidden;
    z-index: 1001;
}

/* Стили для опций BPM */
.bpm-option {
    padding: 12px 0;
    text-align: center;
    transition: all 0.2s ease;
}

/* Предыдущее значение (желтое) */
.bpm-option.previous {
    color: #FFD700;
    font-size: 14px;
}

/* Текущее значение (увеличенное, с обводкой) */
.bpm-option.current {
    color: #FFFFFF;
    font-size: 18px;
    font-weight: bold;
    background: rgba(255, 255, 255, 0.2);
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
}

/* Следующее значение (белое) */
.bpm-option.next {
    color: #FFFFFF;
    font-size: 14px;
}

/* Медиа-запрос для маленьких экранов */
@media (max-width: 480px) {
    .player-menu {
        height: 45px;
        bottom: 10px;
    }

    .scroll-button {
        width: 50px;
        height: 30px;
    }

    .bpm-value {
        font-size: 12px;
    }

    .bpm-label {
        font-size: 8px;
    }
}